      info3:'jfkljffkljsadflkalkkljfkladjflkadsjflkdsfjldksjfksjfkjflkasdjfklsjdfkjdkjalkjflkdsajflkasjlfkjlfkdsjfkllsklakdsjfkldsjfjsfklsadj',
      processUrl3:'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg',
      // }
    };
  },
  methods:{
    updata(){
      axios.post('/case/info',qs.stringify({
        caseId:this.$route.query.caseId
      })).then((res)=>{
        console.log('res:',res)
        if (res.data.code === 200){   // 修改
          this.movieUrl = res.data.data.movieUrl
          this.playerOptions['sources'][0]['src'] = this.movieUrl
          this.age = res.data.data.age
          this.caseId=res.data.data.caseId
          this.animalName = res.data.data.animalName
          this.breathe = res.data.data.breathe
          this.caseinfo = res.data.data.caseinfo
          this.heartRate = res.data.data.heartRate
          this.imageUrl = require(res.data.data.imageUrl)
          // this.imageUrl = res.data.data.imageUrl
          this.info1 = res.data.data.info1
          this.info2 = res.data.data.info2
          this.info3 = res.data.data.info3
          this.processUrl1 = res.data.data.processUrl1
          this.processUrl2 = res.data.data.processUrl2
          this.processUrl3 = res.data.data.processUrl3
          this.temper = res.data.data.temper
        }
        else if (res.data.code === -1) {
          window.sessionStorage.clear(); // 清空token
          var href = res.data.data.domain;
          window.location.href = href;
        }
        else {
          this.$store.dispatch('snackbar/openSnackbar',{
            msg:res.data.msg,
            color:'primary'
          })
        }
      }).catch((err)=>{
        console.log(err);
      })
    },
    handleClose(){
      this.dialog = false
      const video = document.getElementById('video')
      video.pause()
    }
  }
};
</script>

